import React, { Component } from 'react'
// import '../style.css'

class CardDetails extends Component {

    

    render() {
        const myCountryImg = {
            width: '60%'
        }

        const myNameCard = {
            margin: '25px',
            // backgroundColor : '#393738',
            width: '35%',
            padding: '15px',
            backgroundColor: '#282c34',
            color: 'white'
        }

        const myCardInfo = {
            display: 'block'
        }

        const myCardInfoDetail ={
            display: 'inline-block',
            width: '45%',
        }

        return (
            
        <div className='popup'>    
          <div className='popup_inner'>
            <div>
              <h1>India</h1>
            </div>
            <div>
              <img
                style={myCountryImg}
                src="https://restcountries.eu/data/ind.svg"
                alt="5425"
              />
            </div>
            <div className="nameCard" style={myNameCard}>
              <h3>Name</h3>
              <div style={myCardInfo}>
                <p style={myCardInfoDetail}>Common</p>
                <p style={myCardInfoDetail}>Common</p>
              </div>
              <div style={myCardInfo}>
                <p style={myCardInfoDetail}>Official</p>
                <p style={myCardInfoDetail}>Official</p>
              </div>
              <div style={myCardInfo}>
                <p style={myCardInfoDetail}>Common(Native)</p>
                <p style={myCardInfoDetail}>Common(Native)</p>
              </div>
              <div style={myCardInfo}>
                <p style={myCardInfoDetail}>Common(Official)</p>
                <p style={myCardInfoDetail}>Common(Official)</p>
              </div>
            </div>

            <div className="geographyCard" style={myNameCard}>
                <h3>Geography</h3>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Region</p>
                    <p style={myCardInfoDetail}>Region</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Subregion</p>
                    <p style={myCardInfoDetail}>Subregion</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Capital</p>
                    <p style={myCardInfoDetail}>Capital</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Demonym</p>
                    <p style={myCardInfoDetail}>Demonym</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Lat/Lng</p>
                    <p style={myCardInfoDetail}>Lat/Lng</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Area</p>
                    <p style={myCardInfoDetail}>Area</p>
                </div>
                <div style={myCardInfo}>
                    <p style={myCardInfoDetail}>Land Borders</p>
                    <p style={myCardInfoDetail}>Land Borders</p>
                </div>
            </div>
            <button onClick={this.props.closePopup}>close me</button>
          </div>
        </div>
        );
    }
}

export default CardDetails
