import React, { Component } from 'react'
import '../App.css'
import CardDetails from './CardDetails'

class InfoCard extends Component {

    constructor(props){
        super(props);
        this.state = {
            showPopup : false
        }
    }

    togglePopup(){
        this.setState({
            showPopup : !this.state.showPopup
        })
    }

    render() {

        const myCard = {
            margin: '25px',
            backgroundColor : '#393738',
            width: '75%',
            padding: '15px',
            cursor: 'pointer'
        }

        const myCardHead1 = {
            fontSize: '25px',
            margin: '0px'
        }

        const myCardHead2 = {
            fontSize: '15px',
            margin: '0px',
            padding: '5px'
        }


        const myCardImg = {
            padding: '20px 0px',
            width: '100%'
        }

        const myFlagImg = {
            padding: '0px',
            width: '100%'
        }

        const myCardDataIcon = {
            textAlign: 'left',
            padding: '5px 10px',
            backgroundColor: '#edf2f4',
            margin: '5px 0'
        }

        const myCardDataInfo = {
            display: 'inline-block',
            padding: ' 0 10px',
        }


        return (

            // <input type="text" placeholder="Search Countries" value={this.state.search} onChange={this.updateSearch.bind(this)}/>

            <div className="Card" style={myCard}>

                  <div>  
                    <h3 style={myCardHead1}>{this.props.dataFromParent.name}</h3>
                    <h5 style={myCardHead2}>{this.props.dataFromParent.nativeName}</h5>
                </div>
                <div className="CardImg" style={myCardImg}>
                <img style={myFlagImg} src={`${this.props.dataFromParent.flag}`} alt=""></img>
                </div>
                <div className="CardData">
                    <div style={myCardDataIcon} className="CardDataIcon">
                        <img style={myCardDataInfo} src={require("../img/icon/languages_32.png")} alt=""/>
                        <div style={myCardDataInfo}>{this.props.dataFromParent.languages[0].name}</div>
                    </div>
                    <div style={myCardDataIcon} className="CardDataIcon">
                        <img style={myCardDataInfo} src={require("../img/icon/money_32.png")} alt=""/>
                        <div style={myCardDataInfo}>{this.props.dataFromParent.currencies[0].name}</div>
                        <div style={myCardDataInfo}>{this.props.dataFromParent.currencies[0].symbol}</div>
                    </div>
                    <div style={myCardDataIcon} className="CardDataIcon">
                        <img style={myCardDataInfo} src={require("../img/icon/phone_32.png")} alt=""/>
                        <div style={myCardDataInfo} >{this.props.dataFromParent.callingCodes}</div> 
                    </div>
                </div>


            </div>
        )
    }
}

export default InfoCard
