import React, { Component } from 'react'
import './App.css';
import axios from 'axios'
import InfoCard from './components/InfoCard';
// import CardDetails from './components/CardDetails'

const api = axios.create({
    baseURL: `https://restcountries.eu/rest/v2/all`
})

class App extends Component {


    constructor(props) {
        

        super(props)
        api.get('/').then( res => {
            // console.log(res.data);
            this.setState({
                country: res.data,
                isActive: true
            })
            
        })

        
        this.state = {
            country: []
        };

        
    }
    

    render() {

        return (
            <div className="App">
                <header className="App-header">
                    <h2>World Index</h2>
                </header>
                {<div className="CountryCard">
                    {this.state.country.map((country,index) => <InfoCard key={country.name} dataFromParent = {this.state.country[index]} clickHandler={this.state.isActive}/>)}
                </div>}
                
                {/* <CardDetails /> */}
            </div>
        )
    }
}

export default App

